export default {
	data() {
		return {
			codeQuery: null, //二维码参数
			isLoaing: true,
			goodsList: null,
			page: 1,
			recommendGoods: null,
			stopLoad: false,
			shop_name: '我的小店',
			alreadyReq: false,
			storeList: null,
			modelName: '',
			hasAnimate: false,
			message: 0,
			stores: null,
			stopLoadShops: false,
			shopPage: 1,
			otherShopGoods: null,
			TabCur: -1,
			scrollLeft: 0,
			tabs: null
		};
	},
	onLoad(options) {
		console.log(options)
		console.log(options.scene)
		//分享商品进入页面
		if (options.goods_id) {
			uni.setStorageSync('shop_id', options.shop_id)
			uni.navigateTo({
				url: "../goodsDetail/goodsDetail?id=" + options.goods_id + "&user_id=" + options.user_id + "&shop_id=" + options
					.shop_id
			})
		}
		//扫分享二维码进入页面
		if (options.scene) {
			this.codeQuery = options.scene
			uni.setStorageSync('shop_id', this.codeQuery.split('_')[1])
			// this.codeQuery = '1872802_77370' //模拟分享二维码取到的参数
			this.modelName = "tipModel"
		}
		//获取主页商品列表
		this.getGoodsList()
	},
	onShow() {
		//获取导航列表
		// this.getTabs()
	},
	methods: {
		toClassifyPage(i) {
			return
			uni.setStorageSync('classify', this.tabs[i])
			uni.navigateTo({
				url: "../classifyPage/classifyPage"
			})
		},
		// getTabs() {
		// 	uni.request({
		// 		url: this.websiteUrl + "/api/Goods/categoryList",
		// 		method: "POST",
		// 		success: (res) => {
		// 			console.log(res.data)
		// 			if (res.data.status == 0) {
		// 				this.tabs = res.data.data
		// 				return
		// 			}
		// 			this.showToast(res.data.msg)
		// 		},
		// 		fail: () => {
		// 			this.showToast('系统繁忙，请稍后重试！')
		// 		}
		// 	})
		// },
		closeModel() {
			this.modelName = ""
		},
		sureBind() {
			this.setheader()
			uni.request({
				url: this.websiteUrl + '/api/userCenter/getScore',
				data: {
					token: uni.getStorageSync('token')
				},
				header: this.header,
				success: (res) => {
					console.log(res.data)
					if (res.data.status == 1) {
						//session过期或未授权登录
						uni.navigateTo({
							url: "../wxLogin/wxLogin?codeQuery=" + this.codeQuery + '&fromView=newPerson'
						})
						return
					}
					//未过期直接绑定店铺
					this.bindShop()
				}
			})
		},
		bindShop() {
			this.setheader()
			uni.request({
				url: this.websiteUrl + '/api/userCenter/bindShop',
				data: {
					token: uni.getStorageSync('token'),
					user_id: this.codeQuery.split('_')[0],
					shop_id: this.codeQuery.split('_')[1]
				},
				method: "POST",
				header: this.header,
				success: (res) => {
					console.log(res.data)
					this.showToast(res.data.msg)
				}
			})
			this.closeModel()
		},
		//查看更多商品
		more(str) {
			uni.navigateTo({
				url: '../moreGoods/moreGoods?type=' + str
			})
		},
		//跳转商品详情页面
		toDetail(id) {
			uni.navigateTo({
				url: "../../pages/goodsDetail/goodsDetail?id=" + id
			})
		},
		//跳转搜索页面
		toSearch() {
			uni.navigateTo({
				url: "../search/search"
			})
		},
		//跳转设置页面
		toSettings() {
			uni.navigateTo({
				url: "../settings/settings"
			})
		},
		//获取主页商品列表
		getGoodsList() {
			console.log()
			this.setheader(uni.getStorageSync('shop_id'))
			uni.request({
				url: this.websiteUrl + "/api/goods/goodsIndex",
				data: {
					shop_id: uni.getStorageSync('shop_id')
				},
				header: this.header,
				success: (res) => {
					console.log(res.data)
					if (res.data.status == 0) {
						this.goodsList = res.data.data
						if (this.goodsList.shop_name) {
							this.shop_name = this.goodsList.shop_name
							this.tabs = this.goodsList.type_list
							uni.setNavigationBarTitle({
								title: "店铺：" + this.shop_name
							})
						}
						this.isLoaing = false
					}
				}
			})
		}
	}
}
